import {Link} from "react-router-dom";

import "./success.scss";
import checkMark from "../../assets/checkMark.png"

const Success = () => {
  return (
    <div className="success-body">
      <h1 className="success-body_heading"> </h1>
      <p className="success-body_navigation">
        <Link className="success-body_navigation_link" to="/"></Link> - <span> </span>
      </p>
      <div className="success-body_order">
        <div className="success-body_order_inf">
          <div className="success-body_order_inf_body">
            <div className="">
              <img src={checkMark} alt="checkMark" />
            </div>
            <div>
              <p className="success-body_order_inf_p-top">  </p>
              <p className="success-body_order_inf_p-bottom">      !</p>
            </div>
          </div>
        </div>
        <div className="">
          <Link className="" to="/">
            <button className="success-body_order_link_button">  </button>
          </Link> 
        </div>
      </div>
    </div>
  );
};

export default Success;