import {useSelector} from 'react-redux';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import { useNavigate } from "react-router-dom";
import Spinner from "../spinner/Spinner"

import "./checkoutForm.scss";

const CheckoutForm = () => {

  const {sumProducts, addedToCart} = useSelector((state) => state.products);
  const navigate = useNavigate();

  const dataFromTrash = addedToCart.map((item) => ({
    title: item.title,
    count: item.count
  }));

  const renderProductList = (arr) => {
    if (arr.length === 0) {
      return (
              <h5 className="text-center mt-5"><Spinner/></h5>
      )
    }

    return arr.map(({id, price, title, count}) => {

      let croppedName = title;

      if (croppedName.length > 25) {
        croppedName = croppedName.slice(0, 25);
        croppedName = croppedName + '...'
      } 
      
      return (
        <div className="order-details_product" key={id}>
          <p>{croppedName}</p>
          <p>{count * price}$</p>
        </div>
      )
    })
  }

  const elements = renderProductList(addedToCart);

  const SignupSchema = Yup.object({
    name: Yup.string()
            .min(2, ' 2 !')
            .required(' !'),
    email: Yup.string()
            .email(' email !')
            .required(' !'),
    telephone: Yup.number()
            .min(9, '  9')
            .required(' !'),
    country: Yup.string()
            .required(' !'),
    city: Yup.string()
            .required(' !'),
    street: Yup.string()
            .required(' !'),
    house: Yup.string()
            .required(' !'),
    terms: Yup.boolean()
            .required('  !')
            .oneOf([true], '  !')
  })

  return (
      <Formik
        initialValues={{
            name: '',
            email: '',
            telephone: '',
            country: '',
            city: '',
            street: '',
            house: '',
            flat: '',
            text: '',
            terms: false,
            total: sumProducts,
            products: dataFromTrash,
        }}

        validationSchema={SignupSchema}

        onSubmit={values => {
          console.log(JSON.stringify(values, null, 2));
          navigate("/Success");
        }}
      >
        <Form className="form">
          <div className="form_body">
            <div className="form_inputs">

              <h5 className="form_heading"> </h5>
              <div>
                <Field className="form_inputs_input" id="name" name="name" type="text" placeholder=""/>
                <ErrorMessage className="error" name='name' component="div"/>
              </div>
              <div>
                <Field className="form_inputs_input" id="email" name="email" type="email" placeholder="E-mail"/>
                <ErrorMessage className="error" name='email' component="div"/>
              </div>
              <div>
                <Field className="form_inputs_input" id="telephone" name="telephone" type="text" placeholder=""/>
                <ErrorMessage className="error" name='telephone' component="div"/>
              </div>

              <h5 className="form_heading"> </h5>
              <div>
                <Field className="form_inputs_input" id="country" name="country" type="text" placeholder=""/>
                <ErrorMessage className="error" name='country' component="div"/>
              </div>
              <div>
                <Field className="form_inputs_input" id="city" name="city" type="text" placeholder=""/>
                <ErrorMessage className="error" name='city' component="div"/>
              </div>
              <div>
                <Field className="form_inputs_input" id="street" name="street" type="text" placeholder=""/>
                <ErrorMessage className="error" name='street' component="div"/>
              </div>
              <div>
                <Field className="form_inputs_input" id="house" name="house" type="text" placeholder=""/>
                <ErrorMessage className="error" name='house' component="div"/>
              </div>
              <div>
                <Field className="form_inputs_input" id="flat" name="flat" type="text" placeholder=""/>
                <ErrorMessage className="error" name='flat' component="div"/>
              </div>

              <h5 className="form_heading"></h5>
              <div>
                <Field className="form_inputs_textarea" id="text" name="text" as="textarea" placeholder=" "/>
                <ErrorMessage className="error" name='text' component="div"/>
              </div>
            </div>

            <div className="form_order">
              <h5 className="form_heading"> </h5>
              <div className="order-details">
                <div className="order-details_heading">
                  <p></p>
                  <p></p>
                </div>
                {elements}
                <div className="order-details_result">
                  <p></p>
                  <p>{Math.round((sumProducts) * 100) / 100}$</p>
                </div>
              </div>
              
              <h5 className="form_heading"> </h5>
              <div className="form_checkbox_body">
                <label className="checkbox">
                    <Field className="form_checkbox" name="terms" type="checkbox"/>
                     
                </label>
                <ErrorMessage className="error" name='terms' component="div"/>
              </div>
              <button type="submit"> </button>
            </div>
          </div>
        </Form>
      </Formik>
  );
};

export default CheckoutForm;