import { useEffect } from 'react';
import { useDispatch, useSelector} from 'react-redux';
import {shopFetched} from '../../actions';
import {Link} from "react-router-dom";

import Spinner from '../spinner/Spinner';
import "./newollection.scss";

const Newollection = () => {

  const {productsShop, productsShopLoadingStatus} = useSelector((state) => state.products);
  const dispatch = useDispatch();

  useEffect(() => {
    window.scrollTo(0, 0)
  }, []);

  useEffect(() => {
    dispatch(shopFetched(fetch));
  }, [dispatch]);

  if (productsShopLoadingStatus === "loading") {
      return <h5 className="text-center mt-5"><Spinner/></h5>;
  } else if (productsShopLoadingStatus === "error") {
      return <h5 className="text-center mt-5"> </h5>
  }

  const renderProductList = (arr) => {
    if (arr.length === 0) {
      return (
              <h5 className="text-center mt-5"><Spinner/></h5>
      )
    }

    return arr.map(({id,image, price, title}) => {
      if (id <= 3) {
        return (
          <div className="newollection_products_product " key={id}>
            <Link className="newollection_products_product_link" to={`/OneItem/${id}`}>
              <img  className="" src={image}/>
              <div>
                <p className="newollection_products_product_title">{title}</p>
                <div className="newollection_products_product_price">{price}$</div>
              </div>
            </Link>
          </div>
        )
      }
    })
  }

  const elements = renderProductList(productsShop);

  return (
    <div className="newollection">
      <h2 className="newollection_h2"> </h2>
      <div className="newollection_products">
       {elements}
      </div>
      <div className="newollection_button-body">
        <Link to="/shop"><button> </button></Link>
      </div>
    </div>
  )
}

export default Newollection;