import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import {Link} from "react-router-dom";
import { YMaps, Map, Placemark } from '@pbe/react-yandex-maps';

import "./contentontacts.scss";

const Contentontacts = () => {

  const SignupSchema = Yup.object({
    name: Yup.string()
            .min(2, ' 2 !')
            .required(' !'),
    email: Yup.string()
            .email(' email !')
            .required(' !'),
    telephone: Yup.number()
            .min(9, '  9')
            .required(' !'),
    text: Yup.string()
            .min(10, '  10 ')
            .required(' !'),
  })

  return (
    <div  className="contentontacts">
      <h1 className="contentontacts_heading"></h1>
      <p className="contentontacts_navigation">
        <Link className="contentontacts_navigation_link" to="/"></Link> - <span></span>
      </p>
      <div className="contentontacts_contacts">
        <div className="contentontacts_contacts_contact">
          <p></p>
          <p>+7 (888) 823-54-12</p>
        </div>
        <div className="contentontacts_contacts_contact">
          <p>E-mail</p>
          <p>info@sitename.com</p>
        </div>
        <div className="contentontacts_contacts_contact">
          <p></p>
          <p>. ,  , 26/11</p>
        </div>
      </div>
      <div className="contentontacts_map">
        <YMaps className="contentontacts_map_YMaps">
            <Map 
              className="contentontacts_map_YMaps_Map" 
              defaultState={{ center: [55.74769653811567,37.619924806304304], zoom: 17 }}
            >
              <Placemark defaultGeometry={[55.74769653811567,37.619924806304304]} />
            </Map>
        </YMaps>
      </div>

      <div>
        <Formik
          initialValues={{
              name: '',
              email: '',
              telephone: '',
              text: '',
          }}

          validationSchema={SignupSchema}

          onSubmit={values => {
            console.log(JSON.stringify(values, null, 2));
          }}
        >
          <Form>
            <div className="contentontacts_form">
              <h5 className="contentontacts_form_heading"> </h5>
              <div>
                <Field className="contentontacts_form_input" id="name" name="name" type="text" placeholder=""/>
                <ErrorMessage className="error" name='name' component="div"/>
              </div>
              <div>
                <Field className="contentontacts_form_input" id="email" name="email" type="email" placeholder="E-mail"/>
                <ErrorMessage className="error" name='email' component="div"/>
              </div>
              <div>
                <Field className="contentontacts_form_input" id="telephone" name="telephone" type="text" placeholder=""/>
                <ErrorMessage className="error" name='telephone' component="div"/>
              </div>
              <div>
                <Field className="contentontacts_form_textarea" id="text" name="text" as="textarea" placeholder=""/>
                <ErrorMessage className="error" name='text' component="div"/>
              </div>
              <div className="contentontacts_form_button">
                <button type="submit"></button>
              </div>
            </div>
          </Form>
        </Formik>
      </div>
    </div>
  );
};

export default Contentontacts;